	module.exports = {"data":{"markdownRemark":{"html":"<h2>Antiderivatives and Indefinite Integrals</h2>\n<p>Calculating an antiderivative and integrating an indefinite integral are synonymous. An “indefinite” integral is one which is considered across all numerical values, from -∞  to +∞ (i.e. there are no numbers in the upper or lower bound of the integration symbol).</p>\n<p>Integration calculates the area under a particular curve, that which is defined in the integrand. Indefinite interals consider the areas of infinitely many, infinitely small rectangles to calculate the area under a particlar curve.</p>\n<p>Real-life applications of Antiderivatives and Indefinite Integrals are commonly found in physics, where one can integrate a given velocity to calculate how far a particular object has moved (displacement). </p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://www.intmath.com/integration/2-indefinite-integral.php\">Helpful article regarding Antiderivatives and Indefinite Integrals</a></li>\n<li><a href=\"http://www.wolframalpha.com/calculators/integral-calculator/\">Wolfram|Alpha is a useful tool for visualizing integration</a></li>\n</ul>","fields":{"slug":"/mathematics/antiderivatives-and-indefinite-integrals/"},"frontmatter":{"title":"Antiderivatives and Indefinite Integrals"}}},"pathContext":{"slug":"/mathematics/antiderivatives-and-indefinite-integrals/"}}

